    text:
      "In the previous emPower Africa article, we talked about how Africa is trembling its way to economic growth, and we came to the conclusion of developing the energy sector being Africas ticket to economic and social prosperity, or more properly a better Africa, and that a better Africa would only happen if both government and investors unite efforts to develop the continents huge electricity capacity/need.Now this might seem very difficultsome would even think its impossibleI mean: were talking about a continent with 600 million people who lack access to electricitythats insane!Well, hold on pessimistic people, we have thought of you! Because in this article we are bringing-up a success story inside Africa: the Mauritius miracle or how to make a big success of a small economy.Availing a political stability, a good governance record and an open/flexible regulatory system, Mauritius is also known for being one of the few countries in the world adhering to what we call: the transitioning energy generation model!What kind of transition are we talking about? Havent you heard yet?The world is transitioning from an industrial age model of electricity generated from large-scale power plants (the current model used in most countries) to a blended energy model that includes distributed electricity generation from both local and regional renewable energy projects. Still, not all countries are going with this flow; some are faster than others in implementing this new energy model, and Mauritius is definitely one of the fastest!In fact, the governments energy policy in Mauritius is very eco-friendly as it highly promotes the use of renewable and clean energy to reduce the countrys dependence on fossil fuels and lower greenhouse gas emissions whose effects are disastrous (read our article Why Green Energy? to know more).The government also has quite an impressive roadmap for its energy generation model by increasing the use of renewable sources of energy from 22% (current percentage) to 35% by 2025. It is willing to do so through launching wind farms, solar energy, biomass and waste-to-energy projects. Mauritius is currently deriving its renewable electricity generation from hydro, wind, landfill gas and solar while maintaining bagasse (sugarcane waste) as primal source of renewable energy (16%) with a total contribution of 22 % in the primary energy supply.Besides, the Mauritian government is constantly looking for international competitive bidding to carry out its energy projects and is concentrating a joint-ventures strategy by encouraging joint ventures between the local private sector and international firms: by combining the local competence with the foreign expertise the country is hoping to meet its development objectives for the energy sector by 2025 and to be one step closer to the futuristic energy model.Moreover, The Central Electricity Board (CEB), the main power utility in Mauritius, stated that its planning to upgrade the grid absorption capacity of intermittent energy from 148 MW to160 MW by 2018.Hang on! We have more!The government is also undertaking major legal and institutional reforms in the power sector. For instance, in 2016 the Mauritian government created MARENA (The Mauritius Renewable Energy Agency) as a way to assert that it is taking the development of renewable energy very seriously. Later, in 2017 the government created a Utility Regulatory Authority to regulate and control electricity, potable water and wastewater. Another accomplishment is the Energy Efficiency Act that is aimed to perform product labeling operations plus the importation of specialised energy efficient equipment.If we go on, we could end-up writing a whole journal of the numerous achievements of Africas most competitive economy: Mauritius, so we will end the article by posting a list of sub-sectors in renewable energy generation in which Mauritius is definitely the leader:",
